@import '../variables';

.header {
  color: $blue;
  width: 100%;
  position: relative;
  background-color: $background;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 24px;

  .container {
    display: flex;
  }

  &__logo {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;

    &-wrapper {
      height: 100%;
      min-height: 200px;
      max-height: 250px;
      max-width: 400px;
      flex: 0 0 50%
    }
  }

  &__title {
    padding: 16px 16px 0 0;
    margin: 0;
    text-align: right;
    flex: 0 0 50%;

    &--rotation {
      transform: rotate(-90deg);
      display: inline-block;
    }

    &--size {
      font-size: 3rem;
    }
  }
}

@media screen and (min-width: 568px) {
  .header {
    &__logo-wrapper {
      flex: 1;
    }

    &__title {
      flex: 1;
      text-align: center;
    }
  }
}

@media screen and (min-width: 768px), screen and (orientation: landscape) {
  .header {
    margin-bottom: 24px;

    &__title {
      padding-top: 80px;
      font-size: 3rem;
    }

    &--size {
      font-size: 4rem;
    }
  }
}
